.panorama-daily {
  width: 320px;
  margin: 0 auto;
  position: relative;

  @media (min-width: $tablet-width) {
    width: 660px;
  }

  @media (min-width: $desktop-width) {
    max-width: 1200px;
    width: auto;
  }

  &--banner::before {
    position: absolute;
    content: " ";
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 24px;
    color: #ffffff;
    background-color: #d22856;
    text-transform: uppercase;
    padding: 6px 20px;
    right: 20px;

    @media (min-width: $tablet-width) {
      top: 57px;
    }

    @media (min-width: $desktop-width) {
      right: 130px;
      top: 54px;
    }
  }

  &__author {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
    margin-right: 3px;
  }

  &__time {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 24px;
    color: #283645;
    text-transform: uppercase;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
    margin-bottom: 23px;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      order: 2;
      margin-bottom: 0;

      br {
        display: none;
      }
    }
  }

  &__button {
    width: 13px;
    height: 12px;
    background-image: url("../image/icons/favorite.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    background-position: center;
    margin-right: 18px;
  }

  &__description {
    background-color: #f2f2f2;
    width: 280px;
    margin: 0 auto;
    padding: 25px 10px 25px 17px;
    margin-bottom: 20px;

    @media (min-width: $tablet-width) {
      width: 620px;
      padding: 30px 30px 32px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 12px;
      margin-bottom: 50px;
    }

    @media (min-width: $desktop-width) {
      width: 940px;
      padding: 30px 31px 31px 27px;
    }
  }

  &__image {
    max-width: 100%;
    height: auto;
    display: block;
    object-fit: contain;
  }

  &__title-wrapper {
    margin-bottom: 14px;

    @media (min-width: $tablet-width) {
      order: 2;
      margin-bottom: 0;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;

    @media (min-width: $tablet-width) {
      order: 1;
      grid-column-start: 2;
      grid-row-start: 1;
      justify-content: flex-end;
    }
  }

  &__favorite,
  &__count {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
    color: #d22856;
  }
}
