.main-nav {
  margin: 0 auto;
  position: relative;
}
.main-nav--close .site-list {
  display: none;
}
.main-nav--open .site-list {
  display: block;
}
.main-nav--nojs .site-list {
  display: block;
}
.main-nav .main-nav__container {
  display: flex;
  justify-content: space-between;
  padding: 11px 20px;
  align-items: center;
}
.main-nav .main-nav__container--open {
  background: #283645;
  box-shadow: 0 1px 0 #000000;
}
.main-nav .main-nav__container--close {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav .main-nav__container--nojs {
  background: #283645;
  box-shadow: 0 1px 0 #000000;
}
.main-nav .main-nav__toggle {
  display: block;
  border: none;
  width: 50px;
  height: 24px;
  position: relative;
  background-color: transparent;
}
.main-nav .main-nav__toggle--close::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  border-radius: 1px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
@media (min-width: 660px) {
  .main-nav .main-nav__toggle--close::before {
    top: 4px;
  }
}
.main-nav .main-nav__toggle--open::before, .main-nav .main-nav__toggle--open::after {
  content: "";
  position: absolute;
  top: 13px;
  left: 0;
  width: 23px;
  border-radius: 1px;
  height: 2px;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.main-nav .main-nav__toggle--open::after {
  transform: rotate(-45deg);
}
.main-nav .main-nav__toggle--open {
  width: 23px;
  height: 23px;
}
.main-nav .main-nav__toggle--nojs {
  display: none;
}
.main-nav .main-nav__list {
  position: absolute;
  width: 100%;
}
.main-nav .main-nav__list .site-list__link {
  font-family: "Open Sans Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  padding: 15.5px 0;
  display: inline-block;
  width: 100%;
  position: relative;
}
.main-nav .main-nav__list .site-list__item {
  background: #283645;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
}
.main-nav .main-nav__list .site-list__item:first-child {
  border-top: 1px solid #000000;
}
.main-nav .main-nav__list .site-list__item:last-child {
  border-bottom: none;
}
@media (min-width: 660px) {
  .main-nav .main-nav__container {
    padding: 29px 20px;
  }
}
@media (min-width: 960px) {
  .main-nav {
    width: 960px;
    display: flex;
    padding: 0 10px;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav .main-nav__toggle {
    display: none;
  }
  .main-nav .main-nav__list {
    display: flex;
    position: static;
    width: auto;
    list-style-type: none;
    align-items: center;
  }
  .main-nav .main-nav__list .site-list__item {
    background: transparent;
    box-shadow: none;
    border-bottom: none;
    margin-left: 79px;
  }
  .main-nav .main-nav__list .site-list__item:first-child {
    margin-left: 0;
  }
  .main-nav .main-nav__list .site-list__item .site-list__link {
    padding: 46px 0;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;
  }
  .main-nav .main-nav__container {
    padding: 0;
  }
  .main-nav .main-nav__container--close {
    background-color: transparent;
  }
}
.main-nav .site-list__link:hover {
  color: #d22856;
}
.main-nav .site-list__link:active {
  color: #ffffff;
  opacity: 0.3;
}
@media (min-width: 960px) {
  .main-nav .site-list__link--current::before {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    height: 2px;
    bottom: 30px;
    background-color: #ffffff;
  }
}
.main-nav .main-nav__toggle--open:hover::before,
.main-nav .main-nav__toggle--open:hover::after {
  background-color: #d22856;
}
.main-nav .main-nav__toggle--open:active::before,
.main-nav .main-nav__toggle--open:active::after {
  background-color: #d22856;
  opacity: 0.3;
}
.main-nav .main-nav__toggle--close:hover::before,
.main-nav .main-nav__toggle--close:hover::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav .main-nav__toggle--close:active::before,
.main-nav .main-nav__toggle--close:active::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  opacity: 0.3;
}

