.toggle {
  display: grid;
  grid-template-columns: repeat(3, 6px);
  gap: 10px 19px;
  justify-content: center;
}
.toggle .toggle__slider {
  width: 6px;
  height: 6px;
  background-color: rgba(40, 54, 69, 0.3);
  display: block;
  border-radius: 50%;
  border: none;
}
.toggle .toggle__slider:hover:not(.toggle .toggle__slider--current) {
  background-color: rgba(40, 54, 69, 0.6);
}
.toggle .toggle__slider:active:not(.toggle .toggle__slider--current) {
  background-color: rgba(40, 54, 69, 0.1);
}
.toggle .toggle__slider--current {
  background-color: #ffffff;
  outline: 3px solid #283645;
}
@media (min-width: 960px) {
  .toggle {
    display: none;
  }
}

