.logo {
  @media (min-width: $desktop-width) {
    margin-bottom: 13px;
  }

  &:hover &__pink {
    fill: #d22853;
  }

  &:active &__pink {
    fill: #ffffff;
    opacity: 0.3;
  }

  &:hover &__glasses-all {
    fill: #d22853;
  }

  &:active &__glasses-all {
    fill: #ffffff;
    opacity: 0.3;
  }

  &:hover &__glasses {
    fill: #d22853;
  }

  &:active &__glasses {
    fill: #ffffff;
    opacity: 0.3;
  }

  &:hover &__part-glasses {
    fill: #d22853;
  }

  &:active &__part-glasses {
    fill: #ffffff;
    opacity: 0.3;
  }

  &__glasses {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__part-glasses {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__point-star {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__stars {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__pink {
    transform: translate(-73px, -15%);

    @media (min-width: $tablet-width) {
      transform: none;
    }
  }

  &__glasses-all {
    display: none;

    @media (min-width: $tablet-width) {
      display: block;
    }

    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}
