.promo__text {
  font-family: "Open Sans Light", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 120%;
  color: #ffffff;
  padding: 0 10px;
  padding-bottom: 342px;
  margin-top: 24px;
}

.promo__button {
  border: 3px solid #ffffff;
  border-radius: 29px;
  font-family: "Open Sans Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: #ffffff;
  padding: 15.5px 36px;
  display: inline-block;
  margin-bottom: 32px;

  &:hover {
    background-color: #ffffff;
    color: #d22856;
  }

  &:active {
    color: rgba(210, 40, 86, 0.3);
  }
}

.promo__link {
  width: 50px;
  height: 56px;
  display: block;
  background-repeat: no-repeat;
  background-position: center;

  &--appstore {
    background-image: url("../image/icons/apple-header.svg");
  }

  &--googleplay {
    background-image: url("../image/icons/android-header.svg");
    background-position: center 6px;
  }

  &--microsoft {
    background-image: url("../image/icons/microsoft-header.svg");
    background-position: center 12px;
  }
}

.promo__list {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin-bottom: 28px;
  flex-wrap: wrap;
}

.promo__item {
  margin-right: 30px;
}

.promo__item:last-child {
  margin-right: 0;
}

.promo__description {
  font-family: "Open Sans light", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  margin-bottom: 24px;
  padding: 0 16px;
}

.promo {
  background-image: url("../image/background/hand-promo.png");
  background-repeat: no-repeat;
  background-position: center 110px;

  @media (min-resolution: 2dppx) {
    background-image: url("../image/background/hand-promo@2x.png");
    background-size: 320px 286px;
  }

  @media (min-width: $tablet-width) {
    background-image: url("../image/background/Tablet-hand.png");
    background-size: 660px 545px;
    background-position: center 87px;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/Tablet-hand@2x.png");
      background-size: 660px 545px;
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../image/background/Desktop-hand.png");
    background-size: 1050px 645px;
    background-position: 26px 97px;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/Desktop-hand@2x.png");
    }
  }

  @media (min-width: $tablet-width) {
    padding-left: 20px;
    padding-right: 20px;
    width: 660px;
    margin: 0 auto;

    &__wrapper {
      max-width: 300px;
    }

    &__text {
      margin-top: 54px;
      font-size: 40px;
      margin-bottom: 30px;
      padding: 0 30px;
    }

    &__button {
      margin-bottom: 46px;
      padding: 15.5px 45.7px;
    }

    &__list {
      margin-bottom: 32px;
    }

    &__description {
      margin-bottom: 119px;
      font-size: 18px;
      padding: 0;
    }
  }

  @media (min-width: $desktop-width) {
    & {
      width: 960px;
      padding-top: 50px;
    }

    &__text {
      font-size: 50px;
      line-height: 68px;
    }

    &__button {
      font-size: 21px;
      line-height: 18px;
      padding: 16px 28.5px;
    }
  }
}
