.page-gallary {
  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #283645;
    text-align: center;
    padding: 54px 20px 61px;

    br:nth-child(1) {
      display: none;
    }

    @media (min-width: $tablet-width) {
      font-size: 18px;
      padding: 100px 20px 85px;

      br:nth-child(1) {
        display: block;
      }

      br:nth-child(2) {
        display: none;
      }
    }

    @media (min-width: $desktop-width) {
      padding: 100px 10px 97px;

      br:nth-child(1) {
        display: none;
      }

      br:nth-child(2) {
        display: block;
      }
    }
  }

  &__content {
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 660px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
    }
  }
}
