.gallery {
  &__list {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
      padding: 0 20px;
      width: 660px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(3, 1fr);
      width: 960px;
      padding: 0 10px;
      margin-bottom: 67px;
    }
  }

  &__item {
    width: 320px;
    margin: 0 auto;
    padding: 0 20px;
    margin-bottom: 20px;

    @media (min-width: $tablet-width) {
      padding: 0;
      width: 300px;
      margin-bottom: 0;
      display: flex;
      flex-direction: column;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__content {
    background-color: #f2f2f2;
    padding: 25px 31px 25px 17px;

    @media (min-width: $tablet-width) {
      padding: 30px;
      display: flex;
      flex-direction: column;
      flex-grow: 1;
    }
  }

  &__wrapper-title {
    margin-bottom: 14px;
  }

  &__author {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
  }

  &__time {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 24px;
    color: #283645;
    text-transform: uppercase;
  }

  &__description {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
    margin-bottom: 23px;
  }

  &__favorite,
  &__count {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
    color: #d22856;
  }

  &__button {
    width: 13px;
    height: 12px;
    background-image: url("../image/icons/favorite.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    background-position: center;
    margin-right: 18px;

    @media (min-width: $tablet-width) {
      order: 1;
      margin-left: auto;
      margin-right: 0;
    }
  }

  &__control-wrapper {
    display: flex;
    align-items: center;

    @media (min-width: $tablet-width) {
      margin-top: auto;
    }
  }

  &__image {
    max-width: 100%;
    height: auto;
    display: block;
    object-fit: contain;
  }
}
