.advantage {
  .advantage-wrapper {
    background-color: #f2f2f2;
    background-image: url("../image/background/bevel-corner.svg");
    background-size: 100% 32px;
    background-repeat: no-repeat;

    @media (min-width: $tablet-width) {
      background-size: 100% 50px;
    }

    @media (min-width: $desktop-width) {
      background-size: 100% 92px;
    }

    &__list {
      padding: 87px 20px 288px;
      list-style-type: none;
      display: flex;
      flex-direction: column;
      background-image: url("../image/background/iphone-screen-mobile@1x.png");
      background-repeat: no-repeat;
      background-position: center calc(100% + 43px);
      background-size: 157px 320px;
      flex-wrap: wrap;

      @media (min-resolution: 2dppx) {
        background-image: url("../image/background/iphone-screen-mobile@2x.png");
        background-size: 157px 320px;
      }

      @media (min-width: $tablet-width) {
        align-items: flex-end;
        padding: 67px 20px 0;
        width: 660px;
        margin: 0 auto;
        background-image: url("../image/background/tablet.png");
        background-size: 268px 481px;
        background-position: 20px -3px;

        @media (min-resolution: 2dppx) {
          background-image: url("../image/background/tablet@2x.png");
        }
      }

      @media (min-width: $desktop-width) {
        flex-direction: row;
        padding: 10px 10px 0;
        align-items: stretch;
        justify-content: space-between;
        width: 960px;
        margin: 0 auto;
        background-image: none;
      }

      @media (min-width: $tablet-width) {
        .advantage-wrapper__item:nth-child(2) br:last-child {
          display: block;
        }
      }

      @media (min-width: $desktop-width) {
        .advantage-wrapper__item {
          padding-top: 360px;
        }

        .advantage-wrapper__item:nth-child(2) br:last-child {
          display: none;
        }

        .advantage-wrapper__item--first {
          background-image: url("../image/background/iphone-screen-1-desktop@1x.png");
          background-repeat: no-repeat;
          background-position: center 0;

          @media (min-resolution: 2dppx) {
            background-image: url("../image/background/iphone-screen-1-desktop@2x.png");
            background-size: 292px 302px;
          }
        }

        .advantage-wrapper__item--second {
          background-image: url("../image/background/iphone-screen-2-desktop@1x.png");
          background-repeat: no-repeat;
          background-position: center 0;

          @media (min-resolution: 2dppx) {
            background-image: url("../image/background/iphone-screen-2-desktop@2x.png");
            background-size: 292px 302px;
          }
        }

        .advantage-wrapper__item--third {
          background-image: url("../image/background/iphone-screen-3-desktop@1x.png");
          background-repeat: no-repeat;
          background-position: center 0;

          @media (min-resolution: 2dppx) {
            background-image: url("../image/background/iphone-screen-3-desktop@2x.png");
            background-size: 292px 302px;
          }
        }
      }
    }

    &__title {
      font-family: "Open Sans Bold", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
      margin-bottom: 13px;

      @media (min-width: $tablet-width) {
        text-align: left;
      }

      @media (min-width: $desktop-width) {
        font-family: "Open Sans Bold", sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 30px;
        line-height: 30px;
        max-width: 240px;
        text-align: center;
      }
    }

    &__description {
      font-family: "Open Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      color: #283645;
      text-align: center;
      max-width: 280px;
      margin: 0 auto;
      margin-bottom: 30px;

      @media (min-width: $mobile-width) {
        br {
          display: none;
        }
      }

      @media (min-width: $tablet-width) {
        text-align: left;
        max-width: 310px;
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 33px;
      }

      @media (min-width: $desktop-width) {
        max-width: 252px;
        text-align: center;
        font-size: 18px;
        line-height: 30px;
        margin-bottom: 76px;

        br {
          display: block;
        }
      }
    }
  }
}
