.reviews {
  margin: 0 auto;
  padding: 40px 20px 60px;

  @media (min-width: $tablet-width) {
    padding: 50px 20px 60px;
  }

  @media (min-width: $desktop-width) {
    padding: 193px 10px 60px;
    width: 960px;
  }

  .slider__item {
    display: none;
    flex-direction: column;
    align-items: center;

    &--current {
      display: flex;
    }

    @media (min-width: $desktop-width) {
      width: 700px;
      margin: 0 auto;
    }
  }

  .reviews__message {
    order: 1;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #283645;
    text-align: center;
    margin-bottom: 33px;

    @media (min-width: $tablet-width) {
      font-size: 18px;
      margin-bottom: 36px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 50px;
    }
  }

  .reviews__description {
    display: none;

    @media (min-width: $tablet-width) {
      display: block;
      order: 3;
      font-family: "Open Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 30px;
      color: #283645;
      margin-bottom: 38px;
    }

    @media (min-width: $desktop-width) {
      font-size: 18px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 40px;
    }
  }

  .reviews__name {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 14px;

    @media (min-width: $tablet-width) {
      order: 2;
      font-size: 20px;
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      font-size: 24px;
    }
  }

  .reviews__buttons-control {
    display: none;

    @media (min-width: $desktop-width) {
      position: absolute;
      top: 10%;
      display: flex;
      justify-content: space-between;
      width: 100%;

      .reviews__slider-button {
        width: 22px;
        height: 41px;
        border: none;
        background-color: transparent;

        &--next {
          transform: rotate(180deg);
        }
      }
    }
  }

  .reviews__wrapper {
    @media (min-width: $desktop-width) {
      position: relative;
    }
  }

  &__list {
    position: relative;

    @media (min-width: $desktop-width) {
      &--quotes::before {
        background-image: url("../image/icons/quotes.svg");
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        content: "";
        background-size: 79px 63px;
        top: -98px;
        right: 46%;
        width: 79px;
        height: 63px;
      }
    }
  }

  &__slider-but {
    width: 22px;
    height: 41px;
    opacity: 0.3;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.1;
    }
  }
}
