.page-header {
  display: flex;
  flex-direction: column;
  background-color: $dakrblue;
  text-align: center;
  background-image: url("../image/background/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: 700px 462px;

  @media (min-resolution: 2dppx) {
    background-image: url("../image/background/bg-back-mobile@2x.jpg");
    background-size: 700px 462px;
  }

  @media (min-width: $tablet-width) {
    background-image: url("../image/background/tablet_back.jpg");
    background-size: 1200px 729px;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/tablet_back@2x.jpg");
      background-size: 1200px 729px;
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../image/background/back-desktop.jpg");
    background-size: 2560px 853px;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/back-desktop@2x.jpg");
      background-size: 2560px 853px;
    }
  }
}

@media (min-width: $desktop-width) {
  .page-header__wrapper {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
