.form {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    padding: 0 20px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0 10px;
    width: 960px;
    margin: 0 auto;
    column-gap: 76px;
  }

  &__field {
    border: none;
    padding: 0 20px;
    order: 2;

    @media (min-width: $tablet-width) {
      order: 0;
      border: 2px solid #e5e5e5;
      border-radius: 1px;
      padding: 0;

      &:first-child {
        border: none;

        @media (min-width: $desktop-width) {
          margin-top: 17px;
        }
      }
    }

    &--list {
      background-color: #f2f2f2;
      position: relative;

      @media (min-width: $tablet-width) {
        background-color: transparent;
      }
    }

    &:first-child,
    &:nth-child(4) {
      order: 0;
    }

    &:nth-child(4) {
      @media (min-width: $desktop-width) {
        grid-column: 1/-1;
      }
    }

    &:nth-child(3) {
      order: 1;
      padding-top: 42px;

      @media (min-width: $tablet-width) {
        padding-top: 27px;
        order: 0;
        margin-top: 65px;
        margin-bottom: 51px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 1/-1;
        margin-top: 64px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
      }
    }

    &--text {
      padding: 74px 20px 52px;
      position: relative;

      @media (min-width: $tablet-width) {
        padding: 42px 60px 53px;
        margin-top: 65px;
      }

      @media (min-width: $desktop-width) {
        grid-column: 1/-1;
      }
    }
  }

  &__label {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #d22856;
    text-transform: uppercase;
    display: block;
    padding-left: 23px;

    @media (min-width: $tablet-width) {
      padding-left: 0;
      padding-right: 20px;
    }

    &--second {
      @media (min-width: $tablet-width) {
        padding-left: 17px;
        margin-top: 22px;
      }
    }
  }

  &__input {
    width: 100%;
    padding: 20px;
    border: 2px solid #e5e5e5;
    border-radius: 1px;

    &:hover {
      border: 2px solid #283645;
    }

    &:focus {
      outline: 2px solid #d22856;
    }
  }

  &__group {
    margin-bottom: 29px;

    &:last-child {
      margin-bottom: 44px;

      @media (min-width: $desktop-width) {
        margin-bottom: 0;
      }
    }

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: min-content 1fr;
      align-items: center;
    }
  }

  &__group-second {
    margin-bottom: 29px;
    position: relative;

    @media (min-width: $tablet-width) {
      padding: 0 60px;
      display: flex;
      flex-direction: column-reverse;
    }

    &:last-child {
      margin-bottom: 44px;

      @media (min-width: $desktop-width) {
        margin-bottom: 29px;
      }
    }

    &--telephone::after {
      content: none;

      @media (min-width: $tablet-width) {
        content: "";
        width: 59px;
        height: 59px;
        background-color: #e5e5e5;
        position: absolute;
        top: 0;
        right: 59px;
      }
    }

    &--telephone::before {
      content: none;

      @media (min-width: $tablet-width) {
        background-image: url("../image/icons/phone-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        content: "";
        position: absolute;
        width: 20px;
        height: 19px;
        top: 20px;
        right: 79px;
        z-index: 1;
      }
    }

    &--mail::after {
      content: none;

      @media (min-width: $tablet-width) {
        content: "";
        width: 59px;
        height: 59px;
        background-color: #e5e5e5;
        position: absolute;
        top: 0;
        right: 59px;
      }
    }

    &--mail::before {
      content: none;

      @media (min-width: $tablet-width) {
        background-image: url("../image/icons/mail-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        content: "";
        position: absolute;
        width: 18px;
        height: 13px;
        top: 23px;
        right: 79px;
        z-index: 1;
      }
    }
  }

  &__title {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #d22856;
    text-transform: uppercase;
    text-align: center;

    &--list {
      position: absolute;
      right: 0;
      left: 0;
      top: 39px;

      @media (min-width: $tablet-width) {
        position: static;

        br {
          display: none;
        }
      }
    }

    &--hidden {
      display: none;

      @media (min-width: $tablet-width) {
        display: block;
      }
    }

    @media (min-width: $tablet-width) {
      padding: 0 25px;
    }
  }

  &__list-check {
    list-style-type: none;
    padding-top: 115px;
    padding-bottom: 44px;

    @media (min-width: $tablet-width) {
      padding-top: 49px;
      padding-bottom: 50px;
    }

    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__check-name {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: #283645;
  }

  &__item-check {
    margin-bottom: 47px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $tablet-width) {
      margin-left: 60px;
    }
  }

  &__label-check {
    position: relative;
    display: block;
    padding-left: 59px;
  }

  &__control {
    position: absolute;
    top: -2px;
    left: 0;
    width: 35px;
    height: 35px;
    background: #ffffff;
    border: 2px solid #e5e5e5;
    border-radius: 1px;

    &:hover {
      border: 2px solid #283645;
    }

    &:active {
      border: 2px solid #d22856;
    }
  }

  &__input-check[type="checkbox"]:checked + &__control {
    background: url("../image/icons/checked-form.svg") no-repeat center;
    background-color: #ffffff;
  }

  &__input-control[type="radio"] + &__control {
    border-radius: 50%;
    width: 37px;
    height: 37px;
  }

  &__input-control[type="radio"]:checked + &__control::before {
    position: absolute;
    top: 9px;
    left: 9px;
    width: 15px;
    height: 15px;
    background-color: #d22856;
    border-radius: 50%;
    content: "";
  }

  &__list {
    padding: 111px 0 42px;
    list-style-type: none;

    @media (min-width: $tablet-width) {
      padding: 49px 0 54px;
    }
  }

  &__tag {
    position: relative;
    display: block;
    padding-left: 70px;

    @media (min-width: $tablet-width) {
      padding-left: 59px;
    }
  }

  &__item {
    margin-bottom: 38px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $tablet-width) {
      margin-left: 60px;
    }
  }

  &__large-text {
    width: 100%;
    min-height: 197px;
    border: 2px solid #e5e5e5;
    border-radius: 1px;
    padding: 22px 0 0 20px;
  }

  &__submit {
    display: flex;
    flex-direction: column;
    order: 2;
    padding: 0 20px;

    @media (min-width: $tablet-width) {
      margin-top: 70px;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;
      padding: 0;
    }

    @media (min-width: $desktop-width) {
      grid-column: 1/-1;
    }
  }

  &__submit-button {
    color: #ffffff;
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
    background: #2aab6d;
    border: 2px solid #2aab6d;
    border-radius: 1px;
    padding: 13px 0 12px;
    margin-bottom: 46px;

    &:hover {
      background-color: #1c9b5e;
    }

    &:active {
      background-color: #1c9b5e;
      color: rgba(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
      padding: 13px 54px 12px;
    }
  }

  &__submit-description {
    text-align: center;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    text-transform: uppercase;

    &::first-letter {
      color: #d22856;
    }
  }

  &__hidden-text {
    display: none;

    @media (min-width: $desktop-width) {
      display: inline;
    }
  }
}
