.logo-footer {
  &__glasses {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__part-glasses {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__star {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__all-glasses {
    @media (min-width: $desktop-width) {
      display: none;
    }
  }

  &:hover &__pink {
    fill: #d22856;
  }

  &:active &__pink {
    fill: #283645;
    opacity: 0.3;
  }

  &:active &__all-glasses {
    opacity: 0.3;
  }

  &:active &__glasses {
    opacity: 0.3;
  }

  &:active &__part-glasses {
    opacity: 0.3;
  }

  &:active &__star {
    opacity: 0.3;
  }
}
