.price {
  background-color: #f2f2f2;
  background-image: url("../image/background/bevel-corner.svg");
  background-size: 100% 32px;
  background-repeat: no-repeat;
  padding: 83px 20px 43px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    background-size: 100% 50px;
    padding: 56px 20px 76px;
  }

  @media (min-width: $desktop-width) {
    padding: 100px 10px;
    background-size: 100% 92px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 276px);
    column-gap: 0;
    position: relative;
    width: 832px;
    margin-bottom: 41px;

    @media (min-width: $tablet-width) {
      grid-template-columns: repeat(4, 152px);
      width: 660px;
      position: static;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 233px);
      width: 940px;
      position: static;
      margin: 0 auto;
    }
  }

  &__list {
    list-style-type: none;
  }

  &__list:first-child {
    display: none;

    @media (min-width: $tablet-width) {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }
  }

  &__name {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 30px;
    text-transform: uppercase;
    color: #ffffff;

    @media (min-width: $desktop-width) {
      font-size: 18px;
    }
  }

  &__cell-title {
    background: #283645;
    text-align: center;
    padding: 10px 0 22px;
    border-right: 2px solid #e5e5e5;

    @media (min-width: $tablet-width) {
      padding: 9px 0 6px;
    }

    @media (min-width: $desktop-width) {
      padding: 14px 0 20px;
    }

    &--hit {
      background-image: url("../image/content/hit.png");
      background-repeat: no-repeat;
      background-position: 0 0;

      @media (min-resolution: 2dppx) {
        background-image: url("../image/content/hit@2x.png");
        background-size: 74px 74px;
      }

      @media (min-width: $tablet-width) {
        background-image: url("../image/content/hit-table.png");

        @media (min-resolution: 2dppx) {
          background-image: url("../image/content/hit-table@2x.png");
          background-size: 61px 61px;
        }
      }

      @media (min-width: $desktop-width) {
        background-image: url("../image/content/hit.png");

        @media (min-resolution: 2dppx) {
          background-image: url("../image/content/hit@2x.png");
          background-size: 74px 74px;
        }
      }
    }
  }

  &__coast {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
    color: #ffffff;

    @media (min-width: $desktop-width) {
      font-size: 24px;
    }
  }

  &__named {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &__cell {
    background-color: #ffffff;
    border-right: 2px solid #e5e5e5;
    border-bottom: 2px solid #e5e5e5;
    padding: 18px 0 13px 20px;

    @media (min-width: $tablet-width) {
      padding: 21.5px;
    }

    @media (min-width: $desktop-width) {
      padding: 28.5px;
    }

    &--include {
      background-image: url("../image/icons/include.svg");
      background-repeat: no-repeat;
      background-position: center right 17px;

      @media (min-width: $tablet-width) {
        background-position: center;
      }
    }

    &--uninclude {
      background-image: url("../image/icons/uninclude.svg");
      background-repeat: no-repeat;
      background-position: center right 17px;

      @media (min-width: $tablet-width) {
        background-position: center;
      }
    }
  }

  &__list:nth-child(2) {
    border-left: 2px solid #e5e5e5;
  }

  &__title {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #283645;
    padding: 8px 0 5px 15px;
    background-color: #ffffff;
    border-bottom: 2px solid #e5e5e5;
    border-left: 2px solid #e5e5e5;

    @media (min-width: $desktop-width) {
      font-size: 18px;
      padding: 15px 0 12px 22px;
    }
  }

  &__title:first-child {
    border-top: 2px solid #e5e5e5;
  }
}
