.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;

  &--active {
    display: flex;
  }

  &__container {
    width: 320px;
    background-color: #ffffff;
    box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
    border-radius: 1px;
    position: relative;
    margin: auto;

    @media (min-width: $tablet-width) {
      width: 460px;
    }

    @media (min-width: $desktop-width) {
      width: 780px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding: 44px 20px 52px;
    align-items: center;

    @media (min-width: $tablet-width) {
      padding: 49px 20px 54px;
    }

    @media (min-width: $desktop-width) {
      padding: 49px 0 0;
    }
  }

  &__title {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 37px;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 40px;
      text-align: center;

      br {
        display: block;
      }
    }

    @media (min-width: $desktop-width) {
      br {
        display: none;
      }
    }
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
    margin-bottom: 50px;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 40px;
      font-size: 18px;
      text-align: center;

      br {
        display: block;
      }

      br:nth-child(2) {
        display: none;
      }
    }

    @media (min-width: $desktop-width) {
      br:not(:nth-child(2)) {
        display: none;
      }

      br:nth-child(2) {
        display: block;
      }

      margin-bottom: 68px;
    }
  }

  &__button {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    width: 276px;
    background: #2aab6d;
    border: 2px solid #2aab6d;
    border-radius: 1px;
    color: #ffffff;
    text-transform: uppercase;
    padding: 13px 0 12px;
    display: block;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 296px;
    }
  }

  &__button-wrapper {
    @media (min-width: $desktop-width) {
      background-color: #f2f2f2;
      border-radius: 0 0 5px 5px;
      width: 100%;
      padding: 52px 0;
    }
  }

  &__title-second {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 37px;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      br {
        display: block;
      }
      font-size: 30px;
    }

    @media (min-width: $desktop-width) {
      br {
        display: none;
      }
    }
  }

  &__text-accept {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
    text-align: center;
    margin-bottom: 41px;

    @media (min-width: $tablet-width) {
      font-size: 18px;

      br:nth-child(2),
      br:nth-child(4) {
        display: none;
      }

      br:nth-child(1),
      br:nth-child(3),
      br:nth-child(5) {
        display: block;
      }
    }

    @media (min-width: $desktop-width) {
      br:nth-child(1),
      br:nth-child(3),
      br:nth-child(5) {
        display: none;
      }

      br:nth-child(2),
      br:nth-child(4) {
        display: block;
      }
    }
  }

  &__wrapper-accept {
    background-color: #f2f2f2;
    width: 100%;
    padding: 52px 0;
  }

  &__content-accept {
    padding-top: 44px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
