.promo-second {
  &__text {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    color: #ffffff;

    @media (min-width: $tablet-width) {
      font-size: 50px;
      line-height: 68px;
    }
  }

  &__wrapper {
    padding: 36px 20px 51px;
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 660px;
      padding: 81px 20px 74px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      padding: 74px 10px 104px;
    }

    &--gallery {
      padding: 36px 20px 84px;

      @media (min-width: $tablet-width) {
        padding: 81px 20px 143px;
      }
    }
  }
}
