.wrapper-titles {
  padding: 41px 20px 25px;

  .wrapper-titles__text {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #283645;
    text-align: center;
    margin-bottom: 29px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 54px;
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      font-size: 50px;
      line-height: 68px;
      margin-bottom: 0;
    }
  }

  .wrapper-titles__subtext {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #283645;
    text-align: center;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 54px;
    }

    @media (min-width: $desktop-width) {
      font-size: 50px;
      line-height: 68px;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 42px 36px 38px;
  }

  @media (min-width: $desktop-width) {
    padding: 109px 36px 45px;
  }
}
