.page-footer {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 31px 20px;

    @media (min-width: $tablet-width) {
      flex-direction: row;
      padding: 0 20px 40px;
      min-width: 660px;
      margin: 0 auto;
      justify-content: space-between;
      max-width: 959px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      padding: 70px 10px 75px;
    }
  }

  &__list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px 14px;
    margin-bottom: 42px;
    margin-top: 37px;
  }

  &__link {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: block;
    border: 3px solid rgba(40, 54, 69, 0.1);

    &:hover {
      border: 3px solid #283645;
    }

    &:active {
      border: 3px solid #283645;
      opacity: 0.1;
    }

    &--twitter {
      background-image: url("../image/icons/twitter_footer.svg");
      background-repeat: no-repeat;
      background-position: center;
    }

    &--facebook {
      background-image: url("../image/icons/facebook_footer.svg");
      background-repeat: no-repeat;
      background-position: center;
    }

    &--youtube {
      background-image: url("../image/icons/youtube_footer.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__logo {
    margin-bottom: 41px;
  }

  &__copyright-text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #283645;

    @media (min-width: $desktop-width) {
      font-size: 18px;
    }
  }

  &__copyright {
    text-decoration: none;
    display: flex;
    align-items: center;

    &:hover .page-footer__htmlacademy-icon {
      fill: #283645;
    }

    &:active .page-footer__htmlacademy-icon {
      fill: rgba(40, 54, 69, 0.3);
    }
  }

  &__htmlacademy-icon {
    width: 27px;
    height: 34px;
    margin-left: 13px;
    fill: #d22856;
  }
}
