.map {
  &__frame {
    display: block;
    margin: 0 auto;
    width: 100%;
    min-height: 200px;

    &--nojs {
      display: none;
    }

    @media (min-width: $tablet-width) {
      height: 374px;
    }

    @media (min-width: $desktop-width) {
      height: 502px;
    }
  }

  &__wrapper {
    position: relative;

    &--corner::before {
      content: "";
      position: absolute;
      background-image: url("../image/background/map-corner.svg");
      z-index: 100;
      top: 0;
      left: 0;
      right: 0;
      height: 33px;
      background-size: 100% 33px;
      background-position: 50% 0;
      background-repeat: no-repeat;

      @media (min-width: $tablet-width) {
        height: 67px;
        background-size: 100% 67px;
      }

      @media (min-width: $desktop-width) {
        height: 104px;
        background-size: 100% 104px;
      }
    }
  }

  &__image-nojs {
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 200px;

    @media (min-width: $tablet-width) {
      height: 374px;
    }

    @media (min-width: $desktop-width) {
      height: 502px;
    }

    &--onjs {
      display: block;
    }

    &--nojs {
      display: none;
    }
  }
}
