.advantage-information {
  display: none;

  .advantage-information__wrapper {
    padding: 60px 20px 72px;
    display: flex;
    flex-direction: column;
    width: 660px;
    margin: 0 auto;
    align-items: flex-end;

    @media (min-width: $tablet-width) {
      background-image: url("../image/background/timer.png");
      background-repeat: no-repeat;
      background-position: 20px center;
      padding: 90px 33px 72px;

      @media (min-resolution: 2dppx) {
        background-image: url("../image/background/timer@2x.png");
        background-size: 238px 276px;
      }
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      padding: 17px 10px 67px;

      background-image: url("../image/background/timer_desktop.png");
      background-position: 10px center;
      background-size: 298px 345px;

      @media (min-resolution: 2dppx) {
        background-image: url("../image/background/timer_desktop@2x.png");
        background-size: 298px 345px;
      }
    }
  }

  .advantage-information_content {
    max-width: 310px;

    @media (min-width: $desktop-width) {
      max-width: 573px;
    }

    .advantage-information__title {
      font-family: "Open Sans Bold", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 30px;
      line-height: 36px;
      margin-bottom: 10px;
      margin-top: 15px;

      @media (min-width: $desktop-width) {
        line-height: 30px;
        margin-top: 98px;

        br {
          display: none;
        }
      }
    }

    .advantage-information__description {
      font-family: "Open Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      color: #283645;
      margin-bottom: 10px;

      @media (min-width: $tablet-width) {
        display: block;

        br:nth-child(2) {
          display: none;
        }
      }

      @media (min-width: $desktop-width) {
        font-size: 18px;
        line-height: 30px;

        br:nth-child(1),
        br:nth-child(3),
        br:nth-child(4) {
          display: none;
        }

        br:nth-child(2) {
          display: block;
        }
      }
    }

    .advantage-information__link {
      font-family: "Open Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      text-decoration-line: underline;
      color: #d22856;

      @media (min-width: $desktop-width) {
        font-size: 18px;
        line-height: 30px;
      }
    }
  }

  @media (min-width: $tablet-width) {
    display: block;
  }

  @media (min-width: $desktop-width) {
    background-color: #f2f2f2;
  }
}
