.contacts {
  padding: 47px 20px 0;

  @media (min-width: $tablet-width) {
    width: 660px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 960px;
    margin: 0 auto;
    padding: 47px 10px 41px;
  }

  &__descripteon {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    color: #283645;
    text-align: center;
    margin-bottom: 34px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 54px;
      margin-bottom: 31px;
    }

    @media (min-width: $desktop-width) {
      font-size: 50px;
      line-height: 68px;
      margin-bottom: 61px;
    }
  }

  &__list {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 41px;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      justify-items: center;
      column-gap: 20px;
      margin-bottom: 24px;
    }

    @media (min-width: $desktop-width) {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: flex-start;
    }
  }

  &__title {
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    color: #283645;
    text-align: center;

    @media (min-width: $tablet-width) {
      margin-bottom: 3px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 34px;
    }
  }

  &__phone {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    color: #d22856;
    display: inline-block;
    margin-bottom: 17px;
    text-decoration: none;

    &:hover {
      color: #283645;
      text-decoration: underline;
    }

    &:active {
      color: rgba(40, 54, 69, 0.3);
      text-decoration: none;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }
  }

  &__mail {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    color: #d22856;
    display: inline-block;
    margin-bottom: 22px;
    text-decoration: none;

    &:hover {
      color: #283645;
      text-decoration: underline;
    }

    &:active {
      color: rgba(40, 54, 69, 0.3);
      text-decoration: none;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }
  }

  &__address-text {
    font-family: "Open Sans Light", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    color: #d22856;
    text-align: center;
  }

  &__item:nth-child(2) {
    order: 1;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      margin-top: 42px;
    }

    @media (min-width: $desktop-width) {
      order: 0;
      margin-top: 0;
    }
  }

  &__item {
    @media (min-width: $desktop-width) {
      margin-right: 111px;
    }
  }

  &__item:last-child {
    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }
}
