.page-form {
  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #283645;
    padding: 55px 20px 54px;
    width: 320px;
    margin: 0 auto;
    text-align: center;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      width: 660px;
      padding: 100px 20px 82px;
      font-size: 18px;

      br:nth-child(1),
      br:nth-child(3) {
        display: block;
      }
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      padding: 100px 10px 84px;

      br:not(:nth-child(3)) {
        display: block;
      }

      br:nth-child(1),
      br:nth-child(3) {
        display: none;
      }
    }
  }

  &__hiding-text {
    display: none;

    @media (min-width: $tablet-width) {
      display: inline;
    }
  }
}
