.upload-image {
  &__wrapper {
    padding-top: 49px;

    @media (min-width: $desktop-width) {
      padding-top: 0;
    }
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    text-align: center;
    color: #283645;
    margin-bottom: 52px;

    @media (min-width: $tablet-width) {
      font-size: 18px;
    }
  }

  &__wrapper-editor {
    background-color: #f2f2f2;
    padding: 20px 20px 41px;

    @media (min-width: $tablet-width) {
      padding: 0;
    }

    @media (min-width: $desktop-width) {
      background-color: transparent;
      width: 960px;
      margin: 0 auto;
      padding: 0 10px;
    }
  }

  &__list-button {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 43px;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &__crop {
    width: 48px;
    height: 48px;
    border: none;
    background-color: transparent;

    &--current &-icon {
      fill: #d22856;
      opacity: 1;
    }

    &--current:hover &-icon {
      fill: #d22856;
      opacity: 1;
    }
  }

  &__fill {
    width: 48px;
    height: 48px;
    border: none;
    background-color: transparent;
  }

  &__contrast {
    width: 48px;
    height: 48px;
    border: none;
    background-color: transparent;
  }

  &__item {
    margin-right: 35px;

    &:last-child {
      margin-right: 0;
    }

    @media (min-width: $tablet-width) {
      margin-right: 0;
    }
  }

  &__image {
    display: block;
    max-width: 100%;
    height: auto;
    object-fit: contain;
    margin: auto;
    width: 280px;

    @media (min-width: $tablet-width) {
      width: 300px;
    }
  }

  &__list-range {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 38px;
    padding-bottom: 34px;
    position: relative;

    &--border::before {
      content: "";
      height: 1px;
      width: 100vw;
      background-color: rgba(40, 54, 69, 0.1);
      position: absolute;
      top: 44px;

      @media (min-width: $tablet-width) {
        content: none;
      }
    }

    @media (min-width: $tablet-width) {
      & li {
        margin-bottom: 27px;
        padding-bottom: 23px;
        border-bottom: 1px solid rgba(40, 54, 69, 0.1);
        padding-left: 10px;
        padding-right: 10px;

        &:last-child {
          margin-bottom: 0;
          border-bottom: none;
        }

        @media (min-width: $desktop-width) {
          margin-bottom: 14px;
        }
      }
    }

    @media (min-width: $tablet-width) {
      margin-top: 0;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
      padding-bottom: 0;
      margin-top: 0;
    }
  }

  &__button-control {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;

    @media (min-width: $tablet-width) {
      margin-top: auto;
    }

    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: 183px 183px;
      column-gap: 44px;
      row-gap: 20px;
      margin-top: auto;
    }
  }

  &__crop-bar {
    display: none;

    &--active {
      display: block;
    }

    @media (min-width: $tablet-width) {
      display: inline-flex;
      align-items: center;
    }
  }

  &__fill-bar {
    display: none;

    @media (min-width: $tablet-width) {
      display: inline-flex;
      align-items: center;
    }
  }

  &__contrast-bar {
    display: none;

    @media (min-width: $tablet-width) {
      display: inline-flex;
      align-items: center;
    }
  }

  &__post {
    width: 100%;
    max-width: 280px;
    margin-bottom: 12px;
    border-radius: 30px;
    border: 3px solid #d22856;
    color: #d22856;
    background-color: transparent;
    font-family: "Open Sans Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    padding: 15px 0 16px;

    &:hover {
      background-color: #d22856;
      color: #ffffff;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      max-width: 260px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;

      &:nth-child(2n+1):last-child {
        grid-column: 1 / span 2;
        text-align: center;
        width: auto;
        max-width: 100%;
      }
    }
  }

  &__reset {
    width: 100%;
    max-width: 280px;
    border-radius: 30px;
    border: 3px solid #283645;
    opacity: 0.1;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    color: #283645;
    padding: 15px 0 16px;

    &:hover {
      opacity: 0.3;
    }

    &:active {
      background-color: #283645;
      color: rgba(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet-width) {
      max-width: 260px;
    }

    @media (min-width: $desktop-width) {
      &:nth-child(2n+1):last-child {
        grid-column: 1 / span 2;
        text-align: center;
        width: auto;
        max-width: 100%;
      }
    }
  }

  &__text-wrapper {
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 620px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
    }
  }

  &__bar {
    appearance: none;
    width: 240px;
    height: 2px;
    border-radius: 5px;
    background: #283645;
    outline: none;
    opacity: 1;

    @media (min-width: $tablet-width) {
      width: 202px;
    }

    @media (min-width: $desktop-width) {
      width: 275px;
    }
  }


  &__bar::-webkit-slider-thumb {
    appearance: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid #d22856;
    background-color: #ffffff;

    &:active {
      background-color: #d22856;
    }
  }

  &__bar::-moz-range-thumb {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid #d22856;
    background-color: #ffffff;

    &:active {
      background-color: #d22856;
    }
  }

  &__content {
    @media (min-width: $tablet-width) {
      display: flex;
      flex-direction: column;
      width: 260px;
    }

    @media (min-width: $desktop-width) {
      width: 410px;
    }
  }

  &__wrapper-form {
    @media (min-width: $tablet-width) {
      width: 660px;
      display: f;
    }
  }

  &__wrapper-content {
    @media (min-width: $tablet-width) {
      display: flex;
      width: 660px;
      margin: 0 auto;
      justify-content: space-between;
      padding: 57px 20px 54px 20px;
    }

    @media (min-width: $desktop-width) {
      width: auto;
      background-color: #f2f2f2;
      padding: 57px 60px 57px 80px;
    }
  }

  &__label {
    &--crop {
      @media (min-width: $tablet-width) {
        background-image: url("../image/icons/crop-table.svg");
        background-repeat: no-repeat;
        background-position: center;

        @media (min-width: $desktop-width) {
          background-image: url("../image/icons/crop-desktop.svg");
        }
      }
    }

    &--fill {
      background-image: url("../image/icons/fill-table.svg");
      background-repeat: no-repeat;
      background-position: center;

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/fill-desktop.svg");
      }
    }

    &--contrast {
      background-image: url("../image/icons/contrast-table.svg");
      background-repeat: no-repeat;
      background-position: center;

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/contrast-desktop.svg");
      }
    }

    @media (min-width: $tablet-width) {
      width: 24px;
      height: 24px;
      margin-right: 32px;
      border: none;
    }

    @media (min-width: $desktop-width) {
      width: 48px;
      height: 48px;
    }
  }

  &__crop-icon {
    width: 44px;
    height: 44px;
    fill: #283645;
    opacity: 0.3;

    &:hover {
      opacity: 0.5;
    }
  }

  &__fill-icon {
    width: 48px;
    height: 43px;
    fill: #283645;
    opacity: 0.3;

    &:hover {
      opacity: 0.5;
    }
  }

  &__contrast-icon {
    width: 32px;
    height: 32px;
    fill: #283645;
    opacity: 0.3;

    &:hover {
      opacity: 0.5;
    }
  }
}
